<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Super Heroes</h4>
      </li>
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        {{employee.name}}
        <div class="chip">{{employee.age}}</div>
        <router-link class="secondary-content"
        v-bind:to="{name:'view-employee',params:{employee_id:employee.age}}">
            <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large orange">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      employees: []
    };
  },
  created() {
    db.collection("employees")
      .orderBy('age').get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            'id': doc.id,
            'name': doc.data().name,
            'age': doc.data().age
          };
          this.employees.push(data);
        });
      });
  }
};
</script>

<style>
</style>


