<template>
  <div id="view-employee">
      <ul class="colllection with-header">
          <li class="collection-header">
              <h4>{{name}}</h4>
          </li>
          <li class="collection-item">
              Age:
              {{age}}
          </li>
          <li class="collection-item">
              ID:
              {{employee_id}}
          </li>
      </ul>
      <router-link to='/' class="btn grey">BACK</router-link>
      <button @click="deleteEmployee" class="btn red">delete</button>
       <div class="fixed-action-btn">
      <router-link v-bind:to="{name:'edit-employee',params:{employee_id: employee_id}}" class="btn-floating btn-large orange">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name:'view-employee',
    data(){
        return{
            employee_id:null,
            name:null,
            age:null
        }
    },
    beforeRouteEnter(to,from,next){
        db.collection('employees').where('employee_id','==',to.params.employee_id)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm =>{
                    vm.employee_id = doc.data().employee_id;
                    vm.age = doc.data().age;
                    vm.name = doc.data().name;
                })
            })
        })
    },
    watch:{
        '$route':'fetchData'
    },
    methods:{
        fetchData(){
            db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.age = doc.data().age
                })
            })
        },
        deleteEmployee(){
            if(confirm('Are you sure you want to delete?')){
                
                db.collection('employees').where('employee_id','==',this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            }
        }
    }
}
</script>

<style>

</style>


